import React, { Fragment } from "react";

const ResumePopup = ({ subnav }) => {
  const [visiblePopup, setVisiblePopup] = React.useState(false);
  const resumeSubnav = React.useRef();

  const toggleVisiblePopup = () => {
    setVisiblePopup(!visiblePopup);
  };
  const handleOutsideClick = (e) => {
    if (!e.path.includes(resumeSubnav.current)) {
      setVisiblePopup(false);
    }
  };
  React.useEffect(() => {
    document.body.addEventListener("click", handleOutsideClick);
  }, []);
  return (
    <li className="list-item subnav" ref={resumeSubnav}>
      {subnav.map((subnav) => (
        <Fragment>
          <div className="list-item__link" onClick={toggleVisiblePopup}>
            {subnav.name}
            <svg
              className={!visiblePopup ? "rotated" : ""}
              width="10"
              height="6"
              viewBox="0 0 10 6"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
                fill="#ffffff"
              />
            </svg>
          </div>

          {visiblePopup && (
            <ul className="subnav__list">
              {subnav.popup.map((item) => (
                <li className="subnav__list-item">
                  <a
                    href={item.link}
                    download={item.download}
                    target={item.target}
                  >
                    {item.text}
                  </a>
                  <a href={item.srcLink} target={item.target}>
                    <p>{item.extension}</p>
                  </a>
                </li>
              ))}
            </ul>
          )}
        </Fragment>
      ))}
    </li>
  );
};

export default ResumePopup;
